/**
 * 绘制带边框并填充的矩形
 * @param ctx
 * @param fillColor
 * @param borderColor
 * @param borderSize
 * @param x
 * @param y
 * @param width
 * @param height
 */
export function renderStrokeFillRect (
  ctx, fillColor, borderColor, borderSize,
  x, y, width, height
) {
  renderFillRect(ctx, fillColor, x, y, width, height)
  renderStrokeRect(ctx, borderColor, borderSize, x, y, width, height)
}

/**
 * 绘制空心矩形
 * @param ctx
 * @param borderColor
 * @param borderSize
 * @param x
 * @param y
 * @param width
 * @param height
 */
export function renderStrokeRect (ctx, borderColor, borderSize, x, y, width, height) {
  ctx.lineWidth = borderSize
  ctx.strokeStyle = borderColor
  ctx.strokeRect(x, y, width, height)
}

/**
 * 绘制填充的矩形
 * @param ctx
 * @param color
 * @param x
 * @param y
 * @param width
 * @param height
 */
export function renderFillRect (ctx, color, x, y, width, height) {
  ctx.fillStyle = color
  ctx.fillRect(x, y, width, height)
}

/**
 * 绘制圆角空心矩形
 * @param ctx
 * @param borderColor
 * @param borderSize
 * @param x
 * @param y
 * @param w
 * @param h
 * @param r
 */
export function renderStrokeRoundRect (ctx, borderColor, borderSize, x, y, w, h, r) {
  ctx.lineWidth = borderSize
  ctx.strokeStyle = borderColor
  renderRoundRect(ctx, x, y, w, h, r)
  ctx.stroke()
}

/**
 * 绘制填充圆角矩形
 * @param ctx
 * @param color
 * @param x
 * @param y
 * @param w
 * @param h
 * @param r
 */
export function renderFillRoundRect (ctx, color, x, y, w, h, r) {
  ctx.fillStyle = color
  renderRoundRect(ctx, x, y, w, h, r)
  ctx.fill()
}

/**
 * 绘制圆角矩形
 * @param ctx
 * @param x
 * @param y
 * @param w
 * @param h
 * @param r
 */
export function renderRoundRect (ctx, x, y, w, h, r) {
  ctx.beginPath()
  ctx.moveTo(x + r, y)
  ctx.arcTo(x + w, y, x + w, y + h, r)
  ctx.arcTo(x + w, y + h, x, y + h, r)
  ctx.arcTo(x, y + h, x, y, r)
  ctx.arcTo(x, y, x + w, y, r)
  ctx.closePath()
}
